import Link from 'next/link'
import { enterPath, isProd } from '@/mode-config'
export default props => {
  const { href } = props
  return (
    <Link
      href={`${href ? `/${href}` : '/'}`}
      as={href ? `${enterPath}${href}` : enterPath}
    >
      {props.children}
    </Link>
  )
}



