export default class Footer extends React.Component {
  render() {
    return (
      <footer className="footer">
        <div id="wrapFooter">
          <section id="footer" className="container">
            <h5 className="copyright">
              <p>
                Copyright ©
                <span className="numFt">2016</span>
                个人版权所有
              </p>
            </h5>
          </section>
        </div>
        <div id="DB_rightQuick" className="scrollGo">
          <ul>
            <li className="fast-item-box">
              <a href="//liuguanhua.github.io/" title="返回首页">
                <i className="fa fa-home fa-2x" />
                <span className="rect-txt-box">返回首页</span>
              </a>
            </li>
            <li className="fast-item-box">
              <a href="javascript:;" title="扫一扫">
                <i className="fa fa-qrcode fa-2x" />
                <span id="qrCode">
                  {
                    <img src={`${require('@/assets/images/qrcode.png')}`} />
                    /* <img src='/static/images/qrcode.png' /> */
                  }
                  <em>手机或iPad浏览</em>
                </span>
              </a>
            </li>
            <li
              className="fast-item-box"
              onClick={() => {
                $('html,body').animate(
                  {
                    scrollTop: 0
                  },
                  800
                )
              }}
              id="goTop"
            >
              <a href="javascript:;" title="返回顶部">
                <i className="fa fa-angle-up fa-2x" />
                <span className="rect-txt-box">返回顶部</span>
              </a>
            </li>
          </ul>
        </div>
      </footer>
    )
  }
}



