import Nav from '@/components/common/Nav'
import Footer from '@/components/common/Footer'
import '@/assets/sass/global'
export default props => (
  <div id="app">
    <Nav />
    <div id="wrapPage" className="view">
      {props.children}
    </div>
    <Footer />
    <style jsx="true">
      {`
        #app {
          min-height: 100%;
          position: relative;
          padding-bottom: 43px;
        }
      `}
    </style>
  </div>
)



