const isProd = process.env.NODE_ENV === 'production'
const filePath = isProd
  ? //?   'http://localhost:1111/resume-ssr/out/next/'
    'https://liuguanhua.github.io/react/next/'
  : '/_next/'
const enterPath = filePath.replace(/\/_next/, '').replace(/\/next/, '')
const staticPath = enterPath + 'static/'
const staticBalePath = filePath + 'static/'

module.exports = {
  isProd,
  filePath,
  enterPath,
  staticPath,
  staticBalePath
}



