import LinkJump from '@/components/common/LinkJump'
class Nav extends React.Component {
  constructor(props) {
    super(props)
  }
  componentDidMount() {
    this.navItemAni()
  }
  navItemAni() {
    const $navBox = $('#nav')
    const $itemActive = $navBox.find('.cur')
    const $itemCurT = $itemActive.position().top
    const $navItem = $navBox.find('li')
    const $navLine = $('#navLine')
    const goInitSite = (num = 0) => {
      $navLine.stop(true, true).animate(
        {
          top: num
        },
        'fast'
      )
    }
    goInitSite()
    $navItem.mouseenter(function() {
      goInitSite($(this).position().top)
    })
    $navBox.mouseleave(() => {
      goInitSite()
    })
  }
  render() {
    return (
      <section id="nav" ref={el => (this.el = el)}>
        {this.props.children}
        <ul>
          <li className="cur">
            <LinkJump>
              <a>
                <i className="fa fa-user fa-fw" />关于<span>/</span>
                <small>me</small>
              </a>
            </LinkJump>
          </li>
          <li
            onMouseEnter={event => {
              $(event.currentTarget)
                .find('em')
                .addClass('shake animated')
            }}
            onMouseLeave={event => {
              $(event.currentTarget)
                .find('em')
                .removeClass('shake animated')
            }}
          >
            <LinkJump href="works">
              <a>
                <i className="fa fa-briefcase fa-fw" />作品<span>/</span>
                <small>works</small>
                <span className="hot">
                  <em />
                </span>
              </a>
            </LinkJump>
          </li>
          <li>
            <a
              href="javascript:;"
              id="navLiThree"
              onClick={() => {
                alert('正在更新......，敬请期待^_^')
              }}
            >
              <i className="fa fa-pencil-square-o fa-fw" />博客<span>/</span>
              <small>blog</small>
            </a>
          </li>
          <li>
            <LinkJump href={'fenav'}>
              <a>
                <i className="fa fa-th fa-fw" />导航<span>/</span>
                <small>nav</small>
              </a>
            </LinkJump>
          </li>
          <li>
            <LinkJump href={'contact'}>
              <a>
                <i className="fa fa-phone fa-fw" />联系<span>/</span>
                <small>contact</small>
              </a>
            </LinkJump>
          </li>
          <div id="navLine" />
        </ul>
      </section>
    )
  }
}

class NavBar extends React.Component {
  constructor(props) {
    super(props)
    this.themeNum = [...Array(5).keys()]
  }
  componentDidMount() {
    this.navWrapDraw()
  }
  varyThemeBg(event) {
    let $this = $(event.currentTarget),
      $bgImg = $this.css('background-image')
    $this
      .addClass('active')
      .siblings()
      .removeClass('active')
    $('#app,.hWrap').css('background-image', $bgImg)
  }
  varyThemeColor(event) {
    const ftColor =
        '#abtTxt h3, #banTxt, #btnDown, #btnThumbsUp, #contactBox .iptFocus label, #feNav' +
        'Box h4, #footer em, .ft-color, #nav li:hover small, #themes i, .ysHov, a:hover,.' +
        'fa-circle{color:',
      // bcColor =   "#DB_rightQuick li, #blogBox .more:hover, #btnMore:active,
      // #btnMore:hover, #contactBox span:after, #headBg, #jobsBox .jobsItem>i, #logo
      // h2,#paging .pagTxt:after, .plbtn,#skillBox .skillW,#worksBox
      // .wTxt,#btnColor,.skill-utils-list h4,{background-color:",
      bcColor =
        '#DB_rightQuick li, #blogBox .more:hover, #btnMore:active, #btnMore:hover, #conta' +
        'ctBox span:after, #headBg, #jobsBox .jobsItem>i, #logo h2,#paging .pagTxt:after,' +
        ' .plbtn,#skillBox .skillW,#worksBox .wTxt,#btnColor,.skill-utils-list h4, #cyc .' +
        'Graph:after,#cyc .Graph:before{background-color:',
      // bdColor = "#round,#themes .active,.skill-list span{border:1px solid ",
      // 			bcColor = "#DB_rightQuick li, #blogBox .more:hover, #btnMore:active,
      // #btnMore:hover, #contactBox span:after, #cyc .Graph:after, #headBg, #jobsBox
      // .jobsItem>i, #logo h2,#paging .pagTxt:after, .plbtn,#skillBox
      // .skillW,#worksBox .wTxt,#btnColor,#cyc .Graph:before{background-color:",
      bdColor =
        '#round,#themes .active,#cyc .outer,#cyc .head,#cyc .foot:after, #cyc .foot:befor' +
        'e,#cyc .hand:after, #cyc .hand:before,#cyc .Graph .point,#cyc .ty:after, #cyc .t' +
        'y:before{border:1px solid ',
      bdRColor = '#navLine{border-right:3px solid ',
      bdLColor1 =
        '#feNavBox a:hover .bdLine:after, #feNavBox a:hover .bdLine:before{border-left:1p' +
        'x solid ',
      bdLColor3 = '#feNavBox h4{border-left:3px solid ',
      bdBColor =
        '#feNavBox a:hover:after, #feNavBox a:hover:before{border-bottom:1px solid '
    let styleCss = '<style id=\'colorThemeTh\' type="text/css">'
    const arrColor = [
      ftColor,
      bcColor,
      bdColor,
      bdRColor,
      bdLColor1,
      bdLColor3,
      bdBColor
    ]
    for (var i = 0; i < arrColor.length; i++) {
      styleCss += arrColor[i] + $(event.currentTarget).val() + '}'
    }
    styleCss += '</style>'
    $('head')
      .find('#colorThemeTh')
      .remove()
      .end()
      .append(styleCss)
  }
  navWrapDraw() {
    const $wrapNav = $('#wrapNav')
    $('#btnToggle').on('click', function() {
      const $this = $(this),
        $btnI = $this.find('i')
      if ($wrapNav.hasClass('navpos')) {
        $this.removeClass('btnActive')
        $wrapNav.removeClass('navpos')
      } else {
        $this.addClass('btnActive')
        $wrapNav.addClass('navpos')
      }
    })
    $('#nav').click('a', () => {
      if ($wrapNav.hasClass('navpos')) {
        $wrapNav.removeClass('navpos')
        $('#btnToggle').removeClass('btnActive')
      }
    })
  }
  render() {
    return (
      <nav>
        <div id="headBg" className="clearfix">
          <button id="btnToggle">
            <span>
              <i />
            </span>
            <span>
              <i />
            </span>
            <span>
              <i />
            </span>
          </button>
        </div>
        <div id="wrapNav" className="wrapNav">
          <Nav>
            <LinkJump>
              <a id="logo" title="刘关化|个人简历|web前端工程师">
                <div id="round">
                  <i className="fa fa-circle" />
                </div>
                <h2>
                  <span>
                    前端
                    <br />
                    一枚
                  </span>
                </h2>
                <img
                  src={`${require('@assets/images/logo.jpg')}`}
                  alt="刘关化|个人简历|web前端工程师"
                />
              </a>
            </LinkJump>
          </Nav>
          <section id="themes" className="clearfix tc">
            <h5>主题背景</h5>
            <div className="ilbk bgImg">
              {this.themeNum.map((item, index) => (
                <button
                  onClick={this.varyThemeBg}
                  key={index}
                >
                  <i className="fa fa-check" />
                </button>
              ))}
            </div>
            <h5>主题颜色</h5>
            <div className="tmBox ilbk">
              <input
                type="color"
                name="color"
                onChange={this.varyThemeColor}
                id="changecolor"
                defaultValue="#8697CB"
              />
              <button id="btnColor" className="bgHov">
                更改
              </button>
              <button
                id="btnReset"
                onClick={() => {
                  $('head')
                    .find('#colorThemeTh')
                    .remove()
                }}
                className="bgHov"
              >
                重置
              </button>
            </div>
          </section>
          <section id="cyc">
            <div className="ty" />
            <div className="vLine" />
            <div className="head clearfix">
              <div className="Graph fl">
                <span className="point" />
              </div>
              <div className="Graph fr">
                <span className="point" />
              </div>
              <div className="eye" />
              <div className="nose" />
              <div className="point" />
            </div>
            <div className="skeleton">
              <div className="outer">
                <div className="hand" />
              </div>
            </div>
            <div className="foot" />
          </section>
        </div>
      </nav>
    )
  }
}

export default NavBar



