import funBeat from '@/assets/script/lib/utils'
import Router from 'next/router'
export default class Header extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      banStyle: {
        start: '#0c0e34',
        end: '#571328'
      }
    }
  }
  componentDidMount() {
    const $banTxt = $('#banTxt')
    const particleground = require('@/assets/script/lib/jquery.particleground')
    const share = require('@/assets/script/lib/jquery.share.min')
    particleground(document.getElementById('header'), {
      dotColor: '#fff',
      lineColor: '#fff',
      density: 10000
    })
    funBeat($banTxt.find('h2'))
    funBeat($banTxt.find('h3'))
    $('#scoialFx').share()
    this.shareAnimate()
    const WOW = require('wowjs').WOW
    new WOW({ live: false }).init()

    Router.onRouteChangeComplete = routePath => {
      if (routePath === '/works' || routePath === '/workswap') {
        this.setState({
          banStyle: {
            start: '#0c0e34',
            end: '#571328'
          }
        })
      } else if (routePath === '/fenav') {
        this.setState({
          banStyle: {
            start: '#0c0e34',
            end: '#1f3c29'
          }
        })
      } else if (routePath === '/contact') {
        this.setState({
          banStyle: {
            start: '#000',
            end: '#005453'
          }
        })
      }
    }
  }
  shareAnimate() {
    const $cShare = $('#scoialFx a')
    const $cLen = $cShare.length
    for (let i = 0; i < $cLen; i++) {
      $cShare
        .eq(i)
        .addClass('wow fadeInUp')
        .attr({
          'data-wow-duration': '1.5s',
          'data-wow-delay': i * 0.5 + 's'
        })
    }
  }

  render() {
    const { isabout } = this.props
    const { start, end } = this.state.banStyle
    return (
      <div id="wrapHeader" className={isabout ? 'hHead' : ''}>
        <header id="header" className={isabout ? 'topWrap' : ''}>
          <div
            id="banner"
            className="banner"
            style={{
              backgroundImage: `linear-gradient(180deg, ${start},${end})`
            }}
          >
            <hgroup id="banTxt" className="ft-white">
              <ul>
                <li>
                  <h2>欢迎来到我的个人主页</h2>
                </li>
                <li>
                  <h3>感觉不错，就分享给你的小伙伴吧 ^_^！</h3>
                </li>
                <li>
                  <div
                    id="scoialFx"
                    className="social-share ilbk"
                    data-disabled="qzone,douban,diandian"
                  />
                </li>
              </ul>
            </hgroup>
          </div>
          {this.props.children}
        </header>
      </div>
    )
  }
}
Header.defaultProps = {
  isabout: false
}



